import { Component, h, Prop, Element, Host } from '@stencil/core';

@Component({
  tag: 'ks-order-address-field',
  styleUrl: 'orderAddressField.css'
})
export class OrderAddressField {
    @Element() root: HTMLElement;
    @Prop() name: string;


    render() {
        return <Host>
            <span>{this.name}</span>
            <p><slot /></p>
        </Host>;
    }
}